import Button from 'react-bootstrap/Button';
import React from 'react';
import InputGroup from 'react-bootstrap/InputGroup';
import Form from 'react-bootstrap/Form';
import SneakerItem from '../../components/Sneaker/SneakerItem';
import Modal from 'react-bootstrap/Modal';
import { connect } from 'react-redux';
import InputMask from "react-input-mask";
import { createOrder } from '../../actions/orders';
import axios from 'axios';
import SearchModal from './search-modal';
import './create-order.css';
import Alert from 'react-bootstrap/Alert';

let mainUrl = 'http://127.0.0.1:8000'

class CreateOrder extends React.Component {
    state = {
        number_phone: '',
        name: '',
        orderPrice: 990,
        delivery: false,
        email: '',
        show: false,
        nameShoes: '',
        markShoes: '',
        isDefect: false,
        shoes_name: [
        ],
        users: [],
        errors: '',
        error: true,
        hideUserForm: true
    }
    makeid = (length) => {
        var result           = '';
        var characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
        var charactersLength = characters.length;
        for ( var i = 0; i < length; i++ ) {
          result += characters.charAt(Math.floor(Math.random() *
        charactersLength));
       }
       return result;
    }

    getErrors = () => {
        const {number_phone, email, name,shoes_name} = this.state;
        if (number_phone === '' || email === '' || name === '') {
            this.setState({
                errors: '  '
            })
        }
        else if ( shoes_name.length <= 0) {
            this.setState({
                errors: '       '
            })
        }
        else{
            this.setState({error: false})
        }
    }

    onChange = e => this.setState({ [e.target.name]: e.target.value })

    setChecked = (value, name) => {
        this.setState({ [name]: value })
    }

    calculateTotalPrice = (shoes_name) => {
        let totalPrice = 0;
        shoes_name.map(item => {
            totalPrice += parseInt(item.orderPrice)
        })
        return totalPrice;
    }

    setActiveUser = (email, number_phone, name) => {
        this.setState({
            email: email, number_phone: number_phone, name: name
        })
        this.setState({ users: [] })
    }

    saveShoes = (e) => {
        const { nameShoes, markShoes, orderPrice, isDefect } = this.state;
        let obj = {
            name: nameShoes,
            mark: markShoes,
            orderPrice: orderPrice,
            isDefect: isDefect,
            unicId: this.makeid(5)
        }
        this.setState(prevState => ({
            shoes_name: [...prevState.shoes_name, obj],
            nameShoes: '',
            markShoes: '',
            orderPrice: 990
        }))
    }

    removeShoes = (id) => {
        let filteredArray = this.state.shoes_name.filter(item => item.unicId !== id)
        this.setState({shoes_name: filteredArray});
    }

    saveOrder = () => {
        
        const { number_phone, delivery, name, shoes_name,error } = this.state;

        let totalPrice = this.calculateTotalPrice(shoes_name);
        const form = { number_phone, delivery, name, shoes_name, totalPrice }

        form.number_phone = parseInt(form.number_phone.replace(/[^0-9]/g, ''), 10);

        if (!error) {
            this.props.createOrder(form, this.props.route);
            this.setState({
                number_phone: '',
                shoesType_id: 1,
                name: '',
                orderPrice: 990,
                delivery: false,
                show: false,
                shoes_name: [],
                nameShoes: '',
                markShoes: '',
                searchWord: ''
            })
            this.props.handleClose()
        }

        this.getErrors();

        

        
    }

    searchUsers = async (e) => {
        const searchWord = e.target.value;
        if (searchWord === '') {
            this.setState({ users: [] })
        }
        else {
            const res = await axios.get(`${mainUrl}/api/search-user/?search=${searchWord}`);
            this.setState({ users: res.data })
        }
        console.log(searchWord)



    }
    render() {
        const { searchWord, email, number_phone, name, delivery, shoes_name, nameShoes, markShoes, isDefect, users, hideUserForm,errors } = this.state;
        return <div>
            <Modal.Header closeButton>
                <Modal.Title> </Modal.Title>
            </Modal.Header>

            <Modal.Body>
                {errors !== '' ? (
                    <Alert  variant="danger">
                        {errors}
                    </Alert>
                ) : ''}

        
                <Form>
                {name && email && number_phone ? (
                        <div>
                        <Alert variant="success">
                        <Alert.Heading>: {name}</Alert.Heading>
                        <p>
                         : {number_phone}
                        </p>
                        <hr />
                        <p className="mb-0">
                            : {email}
                        </p>
                        </Alert>
                        </div>
                    ): ''}
                {!hideUserForm ? (
                        <div>
                            <Form.Group className="mb-3" controlId="formBasicNumberPhone">
                                <Form.Label> </Form.Label>
                                <InputMask mask="7(999)-999-99-99" className="form-control" name="number_phone" value={number_phone} required onChange={this.onChange}>
                                </InputMask>
                            </Form.Group>

                            <Form.Group className="mb-3" controlId="formBasicName">
                                <Form.Label> </Form.Label>
                                <Form.Control name="name" onChange={this.onChange} value={name} type="text" placeholder=" " />
                            </Form.Group>
                            <Form.Group className="mb-3" controlId="formBasicName">
                                <Form.Label>Email</Form.Label>
                                <Form.Control name="email" onChange={this.onChange} value={email} type="text" placeholder="email" />
                            </Form.Group>
                            
                        </div>
                    ) : ''}
                    <Form.Group className="mb-3 search-input-group" controlId="formBasicName">
                        <Form.Label></Form.Label>
                        <InputGroup className="mb-3">

                            <Form.Control className="form-control" value={searchWord} autoComplete="off" name="searchWord" onInput={this.searchUsers} />
                            <Button variant="outline-secondary" onClick={() => this.setState({ hideUserForm: !hideUserForm })}>{hideUserForm ? '/ ' : ''}</Button>
                            {/* <Button variant="outline-secondary">Button</Button> */}
                        </InputGroup>

                        {(users.length > 0) ? (<SearchModal setActiveUser={this.setActiveUser} users={users}></SearchModal>) : ''}

                    </Form.Group>

                   
                    



                    <Form.Group>
                        <div className="mb-3 sneakers-item__grid">
                            {shoes_name.map((item, i) => (
                                <SneakerItem removeShoes={this.removeShoes} key={item.id} item={item}></SneakerItem>
                            ))}
                        </div>
                        <Form.Group>
                            <Form.Label></Form.Label>
                            <Form.Control name="markShoes" value={markShoes} onChange={this.onChange} type="text" placeholder=" " />
                            <Form.Control name="nameShoes" value={nameShoes} onChange={this.onChange} type="text" placeholder=" " />
                            <Form.Select className="mb-3" name="orderPrice" onChange={this.onChange} aria-label="Default select example">
                                <option value="990">EXPRESS -         </option>
                                <option value="2990">STANDART -      ,  , , ,    .</option>
                                <option value="3990">CLASSIC -       ,   ,     ,         .
                                </option>
                                <option value="5490">VIP -       ,     ,     ,         .</option>
                                <option value="7490">BRAND -       Timberland, UGG  CHIRUCA.</option>
                            </Form.Select>
                            <Form.Group className="mb-3" controlId="formBasicCheckbox">
                            <InputGroup className="mb-3">
                                <InputGroup.Checkbox type="checkbox" onChange={() => this.setChecked(!isDefect, "isDefect")} checked={isDefect} label="  ?" />
                                <Form.Label>  ?</Form.Label>
                                <Button variant="outline-secondary" onClick={this.saveShoes} ></Button>
                            </InputGroup>
                            </Form.Group>
                            
                        </Form.Group>
                    </Form.Group>


                    <Form.Group className="mt-3" controlId="formBasicCheckbox">
                        <Form.Check type="checkbox" onChange={() => this.setChecked(!delivery, "delivery")} checked={delivery} label="" />

                    </Form.Group>
                </Form>
            </Modal.Body>

            <Modal.Footer>
                <Button variant="secondary" onClick={this.handleClose}></Button>
                <Button variant="primary" onClick={this.saveOrder}> </Button>
            </Modal.Footer>
        </div>;
    }
}

const mapStateToProps = state => ({
    route: state.status.route
})

export default connect(mapStateToProps, { createOrder })(CreateOrder);